/* eslint-disable class-methods-use-this */
/* eslint-disable react/prefer-stateless-function */
import React, { Component, FC } from 'react';
import { Link } from 'react-router-dom';
import fetcher from '../../fetcherimpl';

interface State {
  novels?: KeyedStorage<NovelMeta>;
  pickups?: KeyedStorage<NovelMeta>;
}

const getLocation = () => window.location.hash.substr(1);

class Top extends Component<{}, State> {
  constructor(prop: any) {
    super(prop);

    this.state = {};
  }

  componentDidMount() {
    fetcher
      .searchNovel('')
      .then((novels) => this.setState({ novels }));

    fetcher
      .searchNovel('filter:pickup')
      .then((pickups) => this.setState({ pickups }));
  }

  shelf() {
    if (!this.state.novels) {
      return (
        <div className="bookshelf">
          <div className="spinner" />
        </div>
      );
    }
    return (
      <div className="bookshelf">
        {
          Object
            .keys(this.state.novels)
            .filter((it) => this.state.novels!.hasOwnProperty(it))
            .map((it) => {
              const item = this.state.novels![it];
              return (
                <div className="book">
                  <img src={item.binding} alt={item.title} className="image" />
                  <div className="detail-container">
                    <div className="upper-items">
                      <div className="title">{item.title}</div>
                      <div className="creators">
                        <div className="author">{item.author}</div>
                        <div className="music-director">{item.author}</div>
                        <div className="graphic-designer">{item.author}</div>
                      </div>
                      <div className="type">短編</div>
                    </div>
                    <div className="summary">
                      {item.summary}
                    </div>
                  </div>
                  <Link to={`/view/${it}`} className="clicktrap" />
                </div>
              );
            })
        }
      </div>
    );
  }

  pickups() {
    if (!this.state.pickups) {
      return (
        <div className="pickups">
          <div className="spinner" />
        </div>
      );
    }
    return (
      <div className="pickups">
        {
          Object
            .keys(this.state.pickups)
            .filter((it) => this.state.pickups!.hasOwnProperty(it))
            .map((it) => {
              const item = this.state.pickups![it];
              return (
                <div className="book">
                  <img src={item.binding} alt={item.title} className="image" />
                  <div className="summary">
                    {item.summary}
                  </div>
                  <Link to={`/view/${it}`} className="clicktrap" />
                </div>
              );
            })
        }
      </div>
    );
  }

  body() {
    const elem = getLocation().split('/')[1];
    switch (elem) {
    case '':
    case undefined:
      return (
        <div className="contents-container">
          <div className="main-content">
            <h2>ピックアップ</h2>
            {this.pickups()}
            <h2>作品一覧</h2>
            {this.shelf()}
          </div>
        </div>
      );
    case 'list':
      return (
        <div />
      );
    default:
      return null;
    }
  }

  render() {
    return this.body();
  }
}

export default (() => <Top />) as FC;
